"use client"

import * as React from "react"

const STORAGE_KEY = "template2.auth.session"

const AuthContext = React.createContext(null)

function readStoredSession() {
  if (typeof window === "undefined") return null
  try {
    const raw = window.localStorage.getItem(STORAGE_KEY)
    if (!raw) return null
    const parsed = JSON.parse(raw)
    if (parsed && typeof parsed === "object") {
      return parsed
    }
  } catch (error) {
    console.warn("[AuthProvider] Failed to parse stored session", error)
  }
  return null
}

export function AuthProvider({ children, initialSession = null }) {
  const [session, setSession] = React.useState(() => initialSession)
  const [isReady, setIsReady] = React.useState(() => Boolean(initialSession))

  React.useEffect(() => {
    if (initialSession) {
      setIsReady(true)
      return
    }

    const stored = readStoredSession()
    if (stored) {
      setSession(stored)
    }
    setIsReady(true)
  }, [initialSession])

  const persistSession = React.useCallback((nextSession) => {
    if (typeof window === "undefined") return
    if (!nextSession) {
      window.localStorage.removeItem(STORAGE_KEY)
      return
    }
    try {
      window.localStorage.setItem(STORAGE_KEY, JSON.stringify(nextSession))
    } catch (error) {
      console.warn("[AuthProvider] Failed to persist session", error)
    }
  }, [])

  const loginWithOpenId = React.useCallback(async (payload = {}) => {
    const response = await fetch("/api/auth/openid", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(payload),
    })

    if (!response.ok) {
      throw new Error("OpenID 로그인 요청에 실패했습니다.")
    }

    const result = await response.json()
    const nextSession = {
      provider: result.provider ?? payload.provider ?? "dummy-openid",
      accessToken: result.accessToken ?? null,
      issuedAt: result.issuedAt ?? new Date().toISOString(),
      user: result.user ?? null,
    }

    setSession(nextSession)
    persistSession(nextSession)
    setIsReady(true)

    return nextSession
  }, [persistSession])

  const logout = React.useCallback(() => {
    setSession(null)
    persistSession(null)
    setIsReady(true)
  }, [persistSession])

  const value = React.useMemo(() => ({
    session,
    user: session?.user ?? null,
    isAuthenticated: Boolean(session?.user),
    isReady,
    loginWithOpenId,
    logout,
  }), [session, isReady, loginWithOpenId, logout])

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>
}

export function useAuth() {
  const context = React.useContext(AuthContext)
  if (!context) {
    throw new Error("useAuth 훅은 AuthProvider 내부에서만 사용할 수 있습니다.")
  }
  return context
}

export function useOptionalAuth() {
  return React.useContext(AuthContext)
}
